import React, { useState } from "react";
import Grid from "@material-ui/core/Grid";
import { Parallax } from "react-parallax";
import background from "../background1.png";
import useMediaQuery from "@material-ui/core/useMediaQuery";

const CoverPanel = () => {
  const large = useMediaQuery("(min-width:1210px)");
  const height = useMediaQuery("(min-width:800px)") ? 900 : 1000;
  console.log(height);
  return (
    <div
      style={{
        position: "relative",
        width: "100%",
        height: `${height}`,
        alignItems: "center",
        justifyContent: "center",
        display: "flex",
      }}
    >
      <Parallax
        bgImage={background}
        style={{
          width: "100%",
          height: `${height}px`,
          zIndex: "-1",
          color: "white",
          display: "flex",
          direction: "column",
          alignItems: "center",
          justifyContent: "center",
        }}
        strength={300}
        bgImageStyle={
          large
            ? { width: "100%", height: "auto" }
            : { width: "auto", height: "100%" }
        }
        bgStyle={{ background: "cover" }}
      >
        <h1
          style={{
            width: "90%",
            margin: "auto",
            marginBottom: "30px",
            letterSpacing: "3px",
            fontSize: "3.5rem",
          }}
        >
          {" "}
          Don't wait for a doctor.
          <br /> Relieve your back pain now.
        </h1>
        <p style={{ fontSize: "1.1rem", width: "50%", margin: "auto" }}>
          Our platform offers a scientific-based program available directly on
          your phone. We help you develop long lasting habits to relieve your
          pain.{" "}
        </p>
      </Parallax>
      <a
        href="https://airtable.com/shrpMYIgGnjtkvvv1"
        style={{
          width: "300px",
          maxWidth: "90%",
          position: "absolute",
        }}
      >
        <div
          style={{
            width: "300px",
            maxWidth: "90%",
            position: "absolute",
            bottom: `-${height / 2 - 50}px`,
            textDecoration: "none",
            padding: "10px",
            backgroundColor: "rgb(1,159,222)",
            borderRadius: "20px",
            boxShadow: "1px 1px 5px black",
            zIndex: 99999999,
          }}
          blank
          target="_blank"
          rel="noreferrer"
          className="coverButton"
        >
          <p style={{ fontSize: "1.2rem", color: "white" }}>
            Subscribe to our waiting list
          </p>
        </div>
      </a>
    </div>
  );
};

export default CoverPanel;

