import React from "react";

function Iframe() {
  return (
    <div
      style={{
        width: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: "rgb(1,159,222)",
        padding: "20px 0",
      }}
    >
      <iframe
        class="airtable-embed"
        src="https://airtable.com/embed/shrpMYIgGnjtkvvv1?backgroundColor=blue"
        frameborder="0"
        onmousewheel="scroll"
        width="533"
        height="533"
        title="airtable"
      ></iframe>
    </div>
  );
}

export default Iframe;
